const express = require('express');
const router = express.Router();

router.get('/',(req,res,next) => {
    res.status(200).json({
        message: "Get Requests"
    })
})

router.post('/',(req,res,next) =>{
    res.status(201).json({
        message: "Post Request"
    })
})

module.exports = router;